#overlay {
  opacity: 1;
}

#overlayBg {
  opacity: 0.9;
  background-color: #222;
}

#overlay,
#overlayBg {
  z-index: 2;
  transition: opacity 0.4s;
  position: fixed;
  height: 100%;
  width: 100%;
}

#overlay.hidden,
#overlay.hidden #overlayBg {
  pointer-events: none;
  opacity: 0;
}

#overlay .card {
  top: 12%;
  width: 75%;
}

#overlay.hidden .card {
  top: 0%;
}

#overlay h1 {
  text-align: center;
  color: #fff;
  top: 3%;
}

#overlay hr {
  width: 50%;
  top: 9%;
  border: 0;
  height: 1px;
  background-image: linear-gradient(
    to right,
    rgba(255, 255, 255, 0),
    rgba(255, 255, 255, 0.75),
    rgba(255, 255, 255, 0)
  );
}

#overlay.hidden hr {
  width: 1%;
}

#overlay .card,
#overlay h1,
#overlay hr {
  position: inherit;
  pointer-events: none;
  z-index: 3;
  left: 0;
  right: 0;
  margin: 0 auto;
  transition: top 0.3s, width 1s;
}
